.header-container.container {
  position: static;
}
.header-container {
    &.container {
        @media screen and (min-width: 1280px) {
            padding: 0 97px 0 46px;
        }
    }
}

.logo {
    @media screen and (max-width: 1279px) {
        margin-right: auto;
    }
    &__svg {
        max-width: 199px;
        max-height: 50px;
    }
}

.header__list {
    display: flex;
    align-items: center;
    padding-top: 6px;
    justify-content: space-between;
    @media screen and (min-width: 768px) {
        padding-top: 37px;
    }
}

@media screen and (max-width: 1279px) {
    .menu-container {
        pointer-events: none;
        opacity: 0;
        position: absolute;
        z-index: 4;
        top: 0;
        right: 0;
        width: 100%;
        height: 502px;
        background-color: $dark-pink;
        @media screen and (min-width: 768px) {
            width: 50%;
        }
        @media screen and (min-width: 768px) {
            height: 399px;
        }
    }
}

.menu-container.is-open {
    opacity: 1;
    transition: opacity 0.5s linear;
    pointer-events: visible;
}

@media screen and (min-width: 1280px) {
    .menu-container {
        .burger--btn-close {
            display: none;
        }
        .header__btn {
            display: none;
        }
    }
    .nav-list {
        display: flex;
        margin: -2px -17px;
        flex-wrap: wrap;
        &__item {
            padding: 2px 17px;
        }
        &__link {
            font-family: $main-font;
            @include font-line(16px, 21px);
            color: $white;
            text-decoration: none;
            &.is--current {
                color: $red;
            }
        }
    }
}

.burger {
    z-index: 2;
    @media screen and (min-width: 1280px) {
        display: none;
    }
}

.burger--btn {
    width: 40px;
    height: 40px;
    cursor: pointer;
    display: flex;
    border: none;
    background-color: transparent;
    &-close {
        margin-top: 10px;
        margin-right: 20px;
        width: 40px;
        height: 40px;
        cursor: pointer;
        display: flex;
        border: none;
        background-color: transparent;
        margin-left: auto;
    }
}

.header__btn {
    @include btn(209px, 44px, $red, $white, 700, 16px, 21px);
    margin-top: 23px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 4;
    &-thd {
        @media screen and (max-width: 767px) {
            display: none;
        }
        @media screen and (min-width: 1280px) {
            margin-right: 18px;
        }
        @include btn(160px, 44px, $red, $white, 700, 16px, 21px);
        display: flex;
        align-items: center;
        justify-content: center;
        margin-left: 30px;
    }
}

.header__btn::after,
.header__btn-thd::after {
    content: "";
    background-image: url(../images/btn-icon.svg);
    display: block;
    margin-left: 16px;
    height: 10px;
    width: 8px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

@media screen and (max-width: 1279px) {
    .nav-list {
        margin-top: 19px;
        &__item {
            padding: 20px 0;
            border-top: 1px solid $border;
            border-bottom: 1px solid $border;
            @media screen and (min-width: 768px) {
                padding: 11px 0;
            }
        }
        &__link {
            padding: 0 30px;
            font-family: $main-font;
            @include font-line(20px, 26px);
            color: $white;
            font-weight: 700;
            text-decoration: none;
            @media screen and (min-width: 768px) {
                @include font-line(20px, 26px);
            }
        }
    }
}