.footer {
    position: relative;
    background-color: #FFF5f6;
}
.layout__footer {
    background-color: #FFF5F6;
    
}

.blocks {
    left: 0;
    transform: translateY(-84px);
    margin-bottom: 54px;
    margin-right: 30px;
    margin-left: 30px;
    @media screen and (min-width: 768px) {
        display: flex;
        justify-content: center;
        transform: translateY(-78px);
        
    @media screen and (min-width: 1280px) {
        transform: translateY(-102px);
    }
    }}


.blocks__item {

    margin-right: 30px;
    margin-left: 30px;

    padding-top: 43px;
    padding-bottom: 43px;
    padding-left: 20px;
    padding-right: 21px;
    margin-bottom: 20px;
    background-color: $white;
    border: 1px solid transparent;
    box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
    border-radius: 24px;
    &:last-child {
        margin-bottom: 0px;
    }
        @media screen and (min-width: 768px) {
           padding-top: 50px;
           padding-bottom: 50px;
           padding-left: 44px;
           padding-right: 31px;
            width: calc((100% - 40px) / 3);
            margin-left: 0px;
        margin-right: 20px;
    &:last-child {
        margin-right: 0px;
        margin-bottom: 20px;
    }

    }
    @media screen and (min-width: 1280px) {
     width: calc((100% - 60px) / 3); 
     &:first-child {
         margin-right: 31px;
     } 
     &:last-child {margin-right: 0px;}
     margin-right: 29px;
    }}


.blocks__item-type {
    margin-bottom: 25px;
    font-size: 12px;
    display: flex;
    width: 80px;
    height: 16px;
    font-weight: 500;
    line-height: 18.6;
    justify-content: center;
    align-items: center;
    color: $white;
    letter-spacing: 0.04em;
    background-color: $sand;
    border-radius: 4px;
    @media screen and (min-width: 768px) {
        width: 73px;
        height: 28px;
        margin-bottom: 30px;
        font-size: 16px;
    }
    @media screen and (min-width: 768px) {
        width: 138px;
height: 28px;
    }
}
.blocks__item-city {
    @media screen and (min-width: 768px) {
        font-size: 14px;
        letter-spacing: 0.04em;
        line-height: 1.86;
        margin-bottom: 10px;
    }
    @media screen and (min-width: 1280px) {
        font-size: 16px;}
}

.blocks__item-type-truck {
    background-color: $green;
    width: 130px;
    @media screen and (min-width: 768px) {
        width: 73px;
        height: 28px;
        margin-bottom: 30px;
        font-size: 16px;}
    @media screen and (min-width: 768px) {
        width: 138px;
height: 28px;
    }
}

.blocks__item-title {
    font-size: 12px;
    font-family: DM Sans, sans-serif;
    font-weight: 500;
    letter-spacing: 0.04em;
    color: $black;
    margin-bottom: 10px;
    text-align: left;
    @media screen and (min-width: 768px) {
        font-size: 14px;
        line-height: 1.86;
    letter-spacing: 0.04em;}
    @media screen and (min-width: 1280px) {
        font-size: 16px;
        
    }
}

.blocks__item-adress {
    font-weight: 500;
    line-height: 1.5;
    color: $grey;
    margin-bottom: 25px;
    white-space: pre-line;
    line-height: 1.5;
    @media screen and (min-width:768px) {
         font-size: 14px;
line-height: 1.66;
    }
    @media screen and (min-width:1280px) {
        font-size: 16px;
        line-height: 1.83;
        letter-spacing: 0.04em;
}

.schedule {
    padding-top: 25px;
    padding-bottom: 25px;
    border-bottom: 1px solid #E1E1E1;
    border-top: 1px solid #E1E1E1;
    margin-bottom: 25px;

    @media screen and (min-width: 768px) {
        margin-bottom: 30px;
    padding-bottom: 30px;}
    @media screen and (min-width: 1280px)
    {margin-bottom: 32px;
    padding-bottom: 32px;}

}
}

.schedule__item-days {
    font-weight: 500;
    line-height: 1.86;
    @media screen and (min-width: 768px) {
        font-size: 14px;
        }
        @media screen and (min-width: 1280px)
        {
            font-size: 16px;
            letter-spacing: 0.04em;
        }
}
.schedule__item {
    
        margin-bottom: 10px;
        &:last-child {padding-bottom: 25px;
            margin-bottom: 25px;
            border-bottom: 1px solid #E1E1E1;}

            @media screen and  (min-width:768px)  {
                margin-bottom:16px;
                &:last-child {
                    padding-bottom: 30px;
                    margin-bottom: 30px;}
            }
            @media screen and  (min-width:1280px) {
                margin-bottom:15px;
                &:last-child {
                    padding-bottom: 32px;
                    margin-bottom: 32px;}
            }
}
.schedule__item-time {
    font-weight: 500;
    line-height: 1.86;
    color: $pink;

@media screen and (min-width: 768px) {
    margin-bottom: 16px;
    font-size: 14px;
    line-height: 1.86;
   

}
 @media screen and (min-width: 1280px){
font-size: 16px;
margin-bottom: 32px;
font-size: 14px;
line-height: 1.86;
&:last-child {margin-bottom: 0px;}
}
}

.blocks__item-contacts {
    font-weight: 500;
    line-height: 1.5;
    color: $grey;
    @media screen and (min-width: 769px) {
        font-size: 14px;
        line-height: 1.83;
    }
    @media screen and (min-width: 1280px) {
        font-size: 16px;
    }
    
}

.blocks__item-tel {
    color: $grey;
}

.blocks__item-email {
    color: $grey;
}


.footer__buttons {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 113px;
    

    @media screen and (min-width: 768px) {
    flex-direction: row;
justify-content: center;}

}


.footer__btn-franchise {
    display: flex;
    align-items: center;
    justify-content: center;
    @include btn ($width:161px, $height:44px, $color:$red, $font-weight:bold, $background:$white, $font-size:16px, $line-height:21px);

    &::after {
        content: "";
        background-image: url(../images/btn-icon.svg);
        display: block;
        margin-left: 16px;
        height: 10px;
        width: 8px;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
    }
    @media screen and (min-width: 768px) {
        
    margin-bottom: 165px;
    width: 191px;
    height: 44px;}
    @media screen and (min-width: 1280px){
       
        width: 180px;
        height: 44px;
      
    }
    }



.footer__btn-locations {
    margin-bottom: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    @include btn ($width:161px, $height:44px, $color:$white, $font-weight:bold, $background:$red, $font-size:16px, $line-height:21px);

    &::after {
        content: "";
        background-image: url(../images/btn-icon-wt.svg);
        display: block;
        margin-left: 16px;
        height: 10px;
        width: 8px;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
    }
        @media screen and (min-width: 768px) {
            margin-bottom: 165px;
            margin-right: 14.5px;
        width: 191px;
    height: 44px;}
            @media screen and (min-width: 1280px){
                width: 180px;
                height: 44px;
            }
  
}
.footer__tagline {
    display: flex;
    flex-direction: column;
}

.footer__tagline-secondary {
    margin-bottom: 43px;
    white-space: pre-line;
    @include large-text ($color: $red, $font-size: 36px, $letter-spacing: 0.04em, $line-height: 1.14);
    @media screen and (min-width: 768px) {@include large-text ($color: $red, $font-size: 48px, $letter-spacing: 0.06em, $line-height: 1.15);}
    @media screen and (min-width: 1280px) {@include large-text ($color: $red, $font-size: 58px, $letter-spacing: 0.06em, $line-height: 1.13);
    margin-bottom:  0px;
white-space: nowrap;}
     
        
    }


.footer__tagline-main {
    white-space: pre-line;
    margin-bottom: 17px;
    width: 232px;
    @include large-text ( $color: $pink, $font-size: 18px, $line-height: 1.16, $letter-spacing: 0);
    @media screen and (min-width: 768px) {
        width: 100%;
        @include large-text ( $color: $pink, $font-size: 30px, $line-height: 1.13, $letter-spacing: 0.04em);
    }
    @media screen and (min-width: 1280px) {@include large-text ($color: $pink, $font-size: 30px, $letter-spacing: 0.06em, $line-height: 1.13);}

}
.footer__social {
    margin-bottom: 43px;
    @media screen and (min-width: 768px) {
        margin-bottom: 175px;
        @media screen and (min-width: 1280px) {
            margin-bottom: 0px;
        }
    }

}
.footer__contacts {
    display: flex;
    justify-content: flex-end;
    line-height: 1.86;
   

    @media screen and (min-width: 768px) {
        font-size: 14px;
    }
    @media screen and (min-width: 1280px) {
        margin-bottom: 0px;
    }
}

.footer__contacts-tel {
    line-height: 1.86;
    color: $red;
    &:hover {
        transform: scale(1.1);
    }
}

.social__list {
    display: flex;

    margin-left: 147px;
    margin-bottom: 46px;

    @media screen and (min-width: 1280px){
margin-bottom: 50px ;
    }
        
    }

.social__link {
    width: 34px;
    height: 34px;
    
}

.footer__link:hover .social__icon,
.footer__link:focus .social__icon {
  fill: $red;
  transform: scale(1.1);
}

.social__icon {
    fill: $pink;
    width: 32px;
    height: 32px;
    margin-right: 28.24px;
    & :last-child {
        margin-right: 0px;
        transition: 350ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
    }
    @media screen and (min-width: 1280px) {
        width: 34px;
        height: 34px;
        margin-right: 30px;
    @media screen and (min-width: 1280px) {

        margin-right: 31px;
    }
}}
.footer__bottom {
    @media screen and (min-width: 768px) {
        display: flex;
        justify-content: space-between;
        margin-bottom: 97px;
    }
    @media screen and (min-width: 1280px) {
        align-items: flex-end;
    }
    
}

.copyrights {
    display: block;
}

.copyrights__text {
    font-size: 14px;
    line-height: 1.52;
    color: $grey;
    padding-top: 22px;
    padding-bottom: 25px;
    padding-left: 79px;
    padding-right: 91px;
    border-top: 1px solid #E1E1E1;
    text-align: center;
    white-space: pre-line;

    @media screen and (min-width: 768px) {
        padding-bottom: 18px;
        padding-top: 29px;
        white-space: nowrap;
    }
    @media screen and (min-width: 1280px) {
        padding-top: 9px;
        padding-bottom: 10px;
    }

}

@media screen and (min-width: 768px) {}

@media screen and (min-width: 1280px) {}