.layout-overlay {
    height: 446px;
    
    @media screen and (min-width: 768px) {
        height: 312px;
    }
    @media screen and (min-width: 1280px) {
        height: 578px;
    }
}

.overlay.container {

    @media screen and (min-width: 1280px) {
    padding: 0 77px;
    }
}

.overlay__sector-one {
    @media screen and (max-width: 767px) {
        position: absolute;
        top: 137px;
        z-index: 2;
    }

    display: flex;
    justify-content: space-between;

    @media screen and (min-width: 768px) {
        padding-top: 12px;
    }

    @media screen and (min-width: 1280px) {
        padding-top: 74px;
    }
}

.overlay__sector-two {
    @media screen and (max-width: 767px) {
        display: none;
    }
    display: flex;
    justify-content: space-between;
    padding-top: 43px;

    @media screen and (min-width: 1280px) {
        padding-top: 60px;
    }

}

.header__btn-first {
    @include btn(62px, 24px, $white, $red, 400, 8px, 10px);

    @media screen and (min-width: 1280px) {
        @include btn(104px, 40px, $white, $red, 400, 14px, 18px);
    }
}

.header__btn-second {
    @include btn(83px, 24px, $red, $white, 400, 8px, 10px);

    @media screen and (min-width: 1280px) {
        @include btn(138px, 40px, $red, $white, 400, 14px, 18px);
    }
}

.header__block {
    display: flex;
    align-items: center;
    justify-content: center;
    @media screen and (min-width: 1280px) {
        height: 191px;
        width: 418px;
    }

    &-list {
        margin-left: 30px;
        @media screen and (max-width: 1279px) {
            margin-left: 0;
            height: 75px;
        } 
        height: 124px;
    }



    &-img {
        object-fit: contain;

        @media screen and (min-width: 768px) {
            width: 124px;
            height: 126px;
            // padding-bottom: 28px;
        }

        @media screen and (min-width: 1280px) {
            width: 188px;
            height: 191px;
            // padding-bottom: 45px;
        }

    }

    &-text {
        font-weight: 400;
        color: $white;

        @include font-line(8px, 12px);
        width: 114px;
        height: 48px;

        @media screen and (min-width: 1280px) {
            @include font-line(14px, 20px);
            width: 200px;
            height: 78px;
        }

    }

    &-btn {
        padding: 0 0;
        margin-top: 20px;
        border: none;
        background-color: transparent;
        width: 26px;
        height: 26px;
        border-radius: 50px;
        cursor: pointer;

        @media screen and (max-width: 1279px) {
            margin-top: 9px;
        }
    }
}


.header__box-title {
    z-index: 2;
    // display: inline-block;
    font-family: $title-font;
    font-weight: 400;
    text-transform: uppercase;
    @include font-line(26px, 30px);
    letter-spacing: 0.04em;
    color: $white;
    &-part {
        display: inline-block;
        @include font-line(48px, 46.32px);
        color: $red;
    }

    @media screen and (min-width: 768px) {
        @include font-line(22px, 25px);
        &-part {
            @include font-line(34px, 31px);
        }
    }
    @media screen and (min-width: 1280px) {
        @include font-line(38px, 44px);
        &-part {
            @include font-line(58px, 52px);

        }
    }
}

.header__box {
    max-width: 218px;
    max-height: 149px;

    @media screen and (min-width: 768px) {
        max-width: 162px;
        max-height: 124px;
        margin-top: 3px;
    }

    @media screen and (min-width: 1280px) {
        max-width: 277px;
        max-height: 202px;
        margin-left: 38px;
        margin-top: 10px ;
    }


    &-container {
        padding-top: 19px;

        @media screen and (min-width: 768px) {
            padding-top: 10px;
        }
    
        @media screen and (min-width: 1280px) {
            padding-top: 18px;
        }
    }
    &-list {
        display: flex;
        margin: 0 -4px;

        @media screen and (min-width: 1280px) {
            margin: 0 -7px;
        }
    }
    &-btn {
        margin: 0 4px;

        @media screen and (min-width: 1280px) {
            padding: 0 7px;
        }

    }
}

.header__count-img {
    @media screen and (max-width: 767px) {
        display: none;
    }
    display: block;
    width: 33px;
    height: 32px;
}



.header__main-img {
    pointer-events: none;

    position: absolute;
    overflow: hidden;
    z-index: 0;
    min-width: 100%;

    @media screen and (max-width: 767px) {
        top: 27px;
        left: 36px;
        
    }
    @media screen and (min-width: 768px) {
        top: -56px;
        left: 304px;
    } 

    @media screen and (min-width: 1280px) {
        top: -35px;
        left: 475px;
    } 

}

.header__png {
    object-fit: contain;
    @media screen and (max-width: 767px) {
        height: 419px;
        min-width: 367px;
    }
    @media screen and (min-width: 768px) {
        height: 368px;
        min-width: 324px;
        width: auto;
    }
    @media screen and (min-width: 1280px) {
        height: 613px;
        min-width: 538px;
        width: auto;
    }

}

.header__pretty-img {
    z-index: 1;

    @media screen and (min-width: 768px) {
        height: 133px;
        min-width: 168px;
        width: auto;
    }
    @media screen and (min-width: 1280px) {
        height: 232px;
        min-width: 293px;
        width: auto;
    }
}

@media screen and (min-width: 1280px) {
    .menu {
        z-index: 2;
    }
}

.count {

    &__list {
        margin: -7px 0;

        @media screen and (max-width: 767px) {
            display: none;
        }
    }

    &__item {
        display: flex;
        flex-direction: column;
        position: relative;
        z-index: 0;
        padding: 7px 0;
    }

    &__numb {
        display: inline-block;
        margin: 0 0;
        padding: 0 0;
        font-family: $title-font;
        @include font-line(28px, 32px);
        letter-spacing: 0.04em;
        text-transform: uppercase;
        color: $red;

        @media screen and (min-width: 1280px) {
            @include font-line(43px, 53px);
        }

    
    }

    &__txt {
        display: inline-block;
        margin: 0 0;
        padding: 0 0;
        font-family: $main-font;
        @include font-line(8px, 10px);
        text-align: right;
        color: $white;

        @media screen and (min-width: 1280px) {
            @include font-line(14px, 18px);
        }
    }
}

.count__item::before{
    content: "";
    position: absolute;
    display: block;
    width: 16px;
    height: 16px;
    border-radius: 50px;
    background-color: $white;
    top: 5px;
    left: 25px;
    z-index: -1;
    @media screen and (min-width: 1280px) {
        width: 30px;
        height: 30px;
        top: 3px;
        left: 40px;
    }
}